<template>
	<div id="app">
		<b-navbar toggleable="lg" type="dark" variant="black">
			<div class="container">
				<div>
					<div class="jo-nav-brand mr-2" :class="{ 'pulse-anim-2' : loading_story }" v-on:click="story()">
						<svg viewBox="0 0 100 100" xmlns="http://www.w3.org/2000/svg" :class="{ 'anim-stroke-draw' : loading_story, 'opened' : is_story_opened}" style="enable-background:new -580 439 577.9 194;" xml:space="preserve">  
							<circle cx="50" cy="50" r="40" />
						</svg>
						<img src="./assets/jhonarendra.png" alt="Putu Jhonarendra Navbar" title="Putu Jhonarendra" class="" />
					</div>
					<b-navbar-brand to="/">
						<span class="title">Jhonarendra</span>
					</b-navbar-brand>
				</div>
				

				<b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

				<b-collapse id="nav-collapse" is-nav>
					<b-navbar-nav class="ml-auto">
						<b-nav-item to="/">Home</b-nav-item>
						<b-nav-item to="/about">About</b-nav-item>
						<b-nav-item to="/project">Project</b-nav-item>
						<b-nav-item to="/blog">Blog</b-nav-item>
						<b-nav-item to="/contact">Contact</b-nav-item>
					</b-navbar-nav>
				</b-collapse>
			</div>
		</b-navbar>
		<router-view :key="$route.fullPath" :is_explore_cd="this.is_explore_cd" @setExplore="setExplore" :pageview="this.pageview"/>
		<!-- <router-view :key="$route.fullPath"/> -->
		
		<div class="story" v-if="loading_story" :class="{ 'active' : show_story , 'hide' : hiding_story }">
			<div class="video-story-wrapper">
				<video id="video-story" class="video-story" muted="" v-on:click="closeStory()">
					<source src="./assets/vid/anjing.mp4" type="video/mp4">
				</video>
				<div class="story-info">
					<div class="story-line"></div>
					<div class="story-line-active"></div>
					<img src="./assets/jhonarendra.png" alt="Putu Jhonarendra Navbar" title="Putu Jhonarendra" />
					<span>jhonarendra</span>
					<span class="story-time ml-2">3h</span>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import firebase from './services/firebase'
export default {
	data(){
		return {
			loading_story: false,
			show_story: false,
			hiding_story: false,
			is_story_opened: false,
			is_explore_cd: true,
			pageview: '',
		}
	},
	methods:{
		story(){
			this.loading_story = true
			setTimeout(() => {
				this.show_story = true
				var vid = document.getElementById('video-story')
				vid.play()
			}, 3000)

			setTimeout(() => {
				this.hiding_story = true
				this.is_story_opened = true
			}, 12300)
			setTimeout(() => {
				this.loading_story = false
				this.show_story = false
				this.hiding_story = false
			}, 12800)
			
		},
		closeStory(){
			this.hiding_story = true
			this.is_story_opened = true
			setTimeout(() => {
				this.loading_story = false
				this.show_story = false
				this.hiding_story = false
			}, 500)
		},
		setExplore(val){
			this.is_explore_cd = val
		},
		getExplore(){
			return this.is_explore_cd
		},
		getPageView(){
			const db = firebase.firestore()
			db.collection('view')
			.get()
			.then((q) => {
				q.forEach((doc) => {
					this.pageview = doc.data().pageview + 1
					this.updatePageView(doc.data().pageview + 1)
				})
			})
			.catch((error) => {
				console.error("Error getting documents: ", error)
			});
		},
		updatePageView(pv){
			const db = firebase.firestore()
			db.collection('view')
			.doc('4m2nyBXcCnUvtgg2Oe3e')
			.update({
				pageview: pv
			})
			.then(() => {
				// console.log("Document successfully updated!")
			})
			.catch((error) => {
				console.error("Error updating document: ", error)
			})
		}
	},
	mounted(){
		this.getPageView()
	}
}
</script>